








import { Component, Vue } from 'vue-property-decorator';
import sessionModule from '@/store/modules/sessionModule'


@Component
export default class Logout extends Vue {

  logout() {
    
    sessionModule
      .logout()
      .then((res:any) =>  {
      this.$router.push({ name: 'Login' });
      }
      )
       
  }
  
}

